:root {
  --font-clash: 'Clash Display', sans-serif;
  --font-satoshi: 'Satoshi', sans-serif;
  --ocre: #CC7722;
  --foret: #224229;
  --creme: #F5F5DC;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-satoshi);
  background-color: var(--foret);
  color: var(--creme);
  overflow-x: hidden;
  line-height: 1.6;
}

body.loading {
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-clash);
  font-weight: 600;
  line-height: 1.2;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(34, 66, 41, 0.2);
}

::-webkit-scrollbar-thumb {
  background: var(--ocre);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #D4892A;
}

/* Text selection */
::-moz-selection {
  background: rgba(204, 119, 34, 0.3);
  color: var(--creme);
}
::selection {
  background: rgba(204, 119, 34, 0.3);
  color: var(--creme);
}

/* Focus styles */
:focus {
  outline: none;
}

:focus-visible {
  outline: 2px solid var(--ocre);
  outline-offset: 2px;
}

/* Utility classes */

/* Animations personnalisées */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes loadingProgress {
  from {
    stroke-dashoffset: 283;
  }
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.animate-fade-in-up {
  animation: fadeInUp 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.animate-scale-in {
  animation: scaleIn 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.animate-slide-in-left {
  animation: slideInLeft 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.animate-slide-in-right {
  animation: slideInRight 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.animate-loading-progress {
  animation: loadingProgress 1.5s ease-out forwards;
}

/* Styles pour les vidéos hero */
.hero-video {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  transform: translate(-50%, -50%);
  z-index: -1;
  filter: brightness(0.6);
}

/* Styles pour les transitions entre sections */
.section-divider {
  position: relative;
  height: 1px;
  background: linear-gradient(90deg, transparent, var(--ocre), transparent);
}

/* Profile tabs styles */
.profile-tab {
  transition: all 0.3s ease;
  opacity: 0.7;
}

.profile-tab.active,
.profile-tab:hover {
  opacity: 1;
  background-color: rgba(245, 245, 220, 0.1);
  transform: translateX(10px);
}

.profile-content {
  display: none;
}

.profile-content.active {
  display: block;
}

.hover\:bg-ocre\/30:hover{
  background-color: rgb(204 119 34 / 0.3);
}

